:root {
	--mui-blue-50: #e3f2fd;
	--mui-blue-100: #bbdefb;
	--mui-blue-200: #90caf9;
	--mui-blue-300: #64b5f6;
	--mui-blue-400: #42a5f5;
	--mui-blue-500: #2196f3;
	--mui-blue-600: #1e88e5;
	--mui-blue-700: #1976d2;
	--mui-blue-800: #1565c0;
	--mui-blue-900: #0d47a1;

	--mui-grey-100: #f5f5f5;
	--mui-grey-200: #eeeeee;
	--mui-grey-300: #e0e0e0;
	--mui-grey-400: #bdbdbd;
	--mui-grey-500: #9e9e9e;
	--mui-grey-800: #424242;
}

.pagination-info {
	margin: 1em 0;
}

.pagination-info-text {
	margin: 0 1em;
	color: var(--mui-blue-800);
}

.pagination-info-text .pagination-info-from-number,
.pagination-info-text .pagination-info-to-number,
.pagination-info-text .pagination-entry-count {
	font-weight: bold;
}

.pagination-info-page-count-selection {
	padding: 0.25em 0.5em;
	font-size: 1em;
	outline: none;
	border-radius: 0%;
	border: 0.05em solid var(--mui-blue-800);
	color: var(--mui-blue-800);
	background-color: var(--mui-blue-50);
	font-weight: bold;
}

.data-table-container {
	overflow-x: auto;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.data-table-filter {
	height: 1.75em;
	font-size: 1em;
	padding: 0.25em 0.5em;
	border: 0;
	outline: none;
	color: var(--mui-blue-800);
	background-color: var(--mui-blue-800);
	font-weight: bold;
}

.data-table-filter:hover {
	background-color: var(--mui-blue-100) !important;
}

.data-table-filter:focus {
	background-color: var(--mui-blue-50);
}

.data-table-filter:focus:invalid {
	color: var(--mui-blue-200);
}

.data-table-filter:hover:invalid {
	color: var(--mui-blue-800);
}

.data-table-filter:valid {
	background-color: var(--mui-blue-200);
}

.data-table-filter:invalid {
	background-color: var(--mui-blue-800);
}

.data-table-filter::placeholder {
	color: var(--mui-blue-200);
}

.data-table-filter:focus::placeholder {
	color: transparent;
}

.data-table {
	color: var(--mui-grey-800);
	border-collapse: collapse;
	font-size: 0.9em;
	width: 100%;
}

.data-table thead th {
	background-color: var(--mui-blue-500);
	color: var(--mui-blue-50);
	text-align: center;
	font-weight: bold;
}

.data-table thead tr:nth-child(1):hover th {
	color: white;
	background-color: var(--mui-blue-700);
}

.data-table thead tr:nth-child(2) th:hover {
	background-color: var(--mui-blue-700);
}

.data-table th,
.data-table td {
	padding: 12px 15px;
}

.data-table tbody td {
	border-bottom: 0.05em solid var(--mui-grey-300);
}

.data-table tbody tr:nth-of-type(odd) td {
	background-color: white;
}

.data-table tbody tr:nth-of-type(even) td {
	background-color: var(--mui-grey-100);
}

.data-table tbody tr:last-of-type {
	border-bottom: 2px solid var(--mui-blue-500);
}

.data-table tbody tr:hover td {
	color: white !important;
	background-color: var(--mui-blue-900) !important;
}

/* sticky 1st column */

.data-table thead th.data-table-header-cell-sticky:nth-child(1),
.data-table thead th.data-table-header-filter-cell-sticky:nth-child(1) {
	background-color: var(--mui-blue-600);
}

.data-table tbody td.data-table-record-cell-sticky:nth-child(1):nth-child(1) {
	font-weight: bold;
	background-color: var(--mui-blue-50);
	color: var(--mui-blue-800);
}

.data-table thead th.data-table-header-cell-sticky:nth-child(1),
.data-table thead th.data-table-header-filter-cell-sticky:nth-child(1),
.data-table tbody td.data-table-record-cell-sticky:nth-child(1) {
	position: sticky;
	left: 0;
}

/* table scroll bar */

.data-table ::-webkit-scrollbar {
	height: 1em;
}

.data-table ::-webkit-scrollbar-track {
	background: var(--mui-grey-200);
}

.data-table ::-webkit-scrollbar-thumb {
	background: var(--mui-grey-400);
}

.data-table ::-webkit-scrollbar-thumb:hover {
	background: var(--mui-grey-500);
}

/* pagination buttons */

.pagination-button-group {
	margin: 1em 0;
}

.pagination-button-group .pagination-button {
	color: var(--mui-blue-500);
	background-color: var(--mui-blue-50);
	border: 0;
	outline: none;
	border-radius: 50%;
	width: 3em;
	height: 3em;
	font-weight: bold;
	cursor: pointer;
}

.pagination-button-group .pagination-button.non-page-button {
	background-color: var(--mui-blue-100);
}

.pagination-button-group .page-button-selected {
	color: white;
	background-color: var(--mui-blue-500);
}

.pagination-button-group .pagination-button:hover {
	color: white;
	background-color: var(--mui-blue-800);
}

.pagination-button-group .pagination-button:disabled,
.pagination-button-group .pagination-button:disabled:hover {
	color: var(--mui-grey-300);
	background-color: var(--mui-grey-100);
	cursor: default;
}

.page-button-group {
	margin: 0.5em;
}
